21px !important;\n    color: $color-gray !important;\n  }\n}\n\n.cost {\n  font-weight: 500;\n  font-size: 18px;\n  line-height: 21px;\n  color: $color-dark;\n\n  @include respond-to(md) {\n    font-size: 20px;\n    line-height: 28px;\n  }\n\n  @include respond-to(lg) {\n    font-size: 32px;\n    line-height: 38px;\n  }\n}\n\n.cost-wrapper {\n  margin-top: 24px;\n  button {\n    margin-top: 24px;\n\n    svg {\n     width: 14px;\n      height: 14px;\n      margin-left: 14px;\n    }\n\n    @include respond-to(sm) {\n      margin-top: 0;\n    }\n  }\n}\n\n.btn-cart {\n  background: $color-orange;\n  color: $color-dark;\n\n  font-weight: normal;\n  font-size: 16px;\n  line-height: 19px;\n  padding: 13px;\n  width: 100%;\n  border-radius: 16px 0;\n  transition: 0.3s;\n\n  &:hover {\n    background: $color-dark;\n    color: #FFFFFF;\n  }\n\n  @include respond-to(sm) {\n    width: auto;\n    padding: 10px 20px;\n  }\n}\n","$color-orange: #F4B12F;\n$color-dark: #1A1819;\n$color-gray: #767676;\n$color-light-gray: #F1F1F1;","@import 'colors';\n\n$container-max-widths: (\n        sm: 100%,\n        md: 740px,\n        lg: 1080px,\n        xl: 1280px,\n);\n$grid-breakpoints: (\n        xs: 0,\n        sm: 576px,\n        md: 767px,\n        lg: 1200px,\n        xl: 1365px,\n);\n\n$font-family-base: 'Rubik', sans-serif;\n$breadcrumb-item-padding-x: 0.1rem;\n$breadcrumb-active-color: $color-orange;\n$breadcrumb-divider-color: $color-gray;\n\n/// Mixin to manage responsive breakpoints\n/// @author Fulliton\n/// @param {String} $breakpoint - Breakpoint name\n/// @require $breakpoints\n@mixin respond-to($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}\n\n@mixin respond-up($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}"],"sourceRoot":""}]);
