ables\";\n\n.btn-rounded-angle {\n  border-radius: 8px 0;\n  padding: 9.5px 14.5px;\n\n  @include respond-to(md) {\n    border-radius: 16px 0;\n    padding: 13px 22px;\n  }\n\n  @include respond-to(lg) {\n    border-radius: 24px 0;\n    padding: 17.5px 27px;\n  }\n}\n\n.btn-rounded {\n  border-radius: 4px;\n  @include respond-to(md) {\n    border-radius: 8px;\n  }\n}\n\n.btn-orange {\n  background-color: $color-orange!important;\n  color: $color-dark!important;\n}\n\n.btn-dark {\n  background-color: $color-dark!important;\n  color: #ffffff!important;\n}\n\n.btn {\n  outline: none;\n  border: none;\n\n  &.btn-big {\n    font-size: 14px;\n\n    @include respond-to(md) {\n      font-size: 16px;\n    }\n\n    @include respond-to(lg) {\n      font-size: 18px;\n    }\n  }\n\n  &.btn-small {\n    font-size: 12px;\n\n    @include respond-to(md) {\n      font-size: 14px;\n    }\n\n    @include respond-to(lg) {\n      font-size: 16px;\n    }\n  }\n\n  &.btn-hover-orange {\n    transition: .5s background-color, .4s color;\n\n    &:hover {\n      background-color: $color-orange!important;\n      color: $color-dark !important;\n    }\n  }\n}\n","@import 'colors';\n\n$container-max-widths: (\n        sm: 100%,\n        md: 740px,\n        lg: 1080px,\n        xl: 1280px,\n);\n$grid-breakpoints: (\n        xs: 0,\n        sm: 576px,\n        md: 767px,\n        lg: 1200px,\n        xl: 1365px,\n);\n\n$font-family-base: 'Rubik', sans-serif;\n$breadcrumb-item-padding-x: 0.1rem;\n$breadcrumb-active-color: $color-orange;\n$breadcrumb-divider-color: $color-gray;\n\n/// Mixin to manage responsive breakpoints\n/// @author Fulliton\n/// @param {String} $breakpoint - Breakpoint name\n/// @require $breakpoints\n@mixin respond-to($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}\n\n@mixin respond-up($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}"],"sourceRoot":""}]);
